.footer
{
    background-color: $negro;
    padding: 5rem 10rem;

    &__logo
    {
        margin: 0 auto;
    }

    &__contenido
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        margin-top: 3rem;
    }

    &__texto
    {
        color: $blanco;
        font-size: 3rem;
        text-transform: uppercase;
        text-align: center;
        margin: 0;

        span
        {
            display: block;
            font-size: 6rem;
        }
    }

    &__socialmedia
    {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet 
        {
            flex-direction: row;
            gap: 5rem;
        }
    }
}

.socialmedia
{

    &__enlace
    {

        img
        {
            width: 4rem;
        }
    }
}