.carta
{
    background-image: url('../img/carta_bg.webp');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    @include desktop 
    {
        background-attachment: fixed;
    }
    margin-top: 10rem;
    position: relative;

    &__mask 
    {
        position: absolute;
        background-color: rgb(255 255 255 / .5);
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &__contenido
    {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10rem 20rem;

        @include tablet 
        {
            padding: 20rem 20rem;
        }
    }

    &__heading
    {
        font-size: 6rem;
        text-transform: uppercase;
    }

    &__texto
    {
        text-transform: uppercase;
        font-size: 3rem;
        text-align: center;
        font-weight: 500;

        @include tablet 
        {
            font-size: 4rem;
        }
    }

    &__boton
    {
        @include boton($negro, $blanco);
        text-transform: uppercase;

        @include tablet 
        {
            width: 50%;
        }
    }
}