.header
{
    position: relative;
    height: 100vh;

    &__video
    {
        position: relative;
        height: 100%;
        overflow: hidden;
    }

    &__video-vid
    {
        position: absolute; 
        height: 100%; 
        width: 100%;  
        padding: 0; 
        top: 0; 
        bottom:0; 
        right: 0; 
        left: 0;
        object-fit: cover;
    }

    &__video-mask
    {
        background: rgb(255 255 255 / .6);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &__contenido
    {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 10rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__titulo
    {
        font-size: 8rem;
        color: $rojoOscuro;
        text-align: center;

        @include tablet 
        {
            font-size: 14.4rem;
        }
    }

    &__opciones
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        @include tablet 
        {
            flex-direction: row;
            gap: 20rem;
        }
    }

    &__enlace
    {
        padding: .5rem 5rem;
        font-size: 2.8rem;
        border: 2px solid $rojo;
        font-weight: 400;
        transition: all .4s ease-in-out;

        &:hover
        {
            background-color: $blanco;
            border: 2px solid $rojoOscuro;
            cursor: pointer;
        }
    }

    &__scroll
    {
        margin-top: 10rem;
        background-color: $blanco;
        padding: 1rem 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid $rojoOscuro;

        i
        {
            font-size: 2.8rem;
            font-weight: 500;
        }

        &:hover
        {
            cursor: pointer;
        }
    }
}

.barra
{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;

    &__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;
    
        @include tablet 
        {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__menu
    {
        text-transform: uppercase;
        font-size: 2.8rem;
        font-weight: 500;

        &:hover
        {
            cursor: pointer;
        }

        @include tablet 
        {
            display: none;
        }
    }
}

.scroll
{
    background-color: rgb(255 255 255 / .2);
    backdrop-filter: blur(.5rem);
    transition: all .3s ease-in-out;
    padding-bottom: 1rem;

    .barra__logo-imagen
    {
        width: 15rem;
    }

    .navegacion__enlace
    {
        font-size: 2rem;
    }
}

.navegacion
{
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
    gap: 2rem;
    margin-top: 3rem;

    @include tablet 
    {
        flex-direction: row;
        gap: 4rem;
        margin-top: 0;
    }

    &__enlace
    {
        font-size: 2.4rem;
        text-transform: uppercase;
        font-weight: 500;
        background-color: $negro;
        width: 100%;
        text-align: center;
        padding: 1rem 10rem;
        color: $blanco;
        transition: all .3s ease-in-out;
        position: relative;

        @include tablet 
        {
            background-color: unset;
            color: $negro;
            padding: 0;
        }

        &:hover
        {
            color: $rojo;
        }

        &--active
        {
            &::after
            {

                @include tablet 
                {
                    content: '';
                    background-color: $rojo;
                    width: 100%;
                    height: .3rem;
                    bottom: -.5rem;
                    left: 0;
                    position: absolute;
                }
            }
        }
    }
}

.no-mostrar
{
    display: none;

    @include tablet 
    {
        display: flex;
    }
}