.opciones
{
    margin-top: 5rem;

    &__contenido
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;
    }
}

.opcion
{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet 
    {
        @include grid(2, 4rem);
        align-items: center;
    }

    &:nth-child(2)
    {
        .opcion__imagen
        {
            grid-column: 2 / 3;
        }

        .opcion__contenido
        {
            grid-row: 1 / 2;
            grid-column: 1 / 2;
        }
    }

    &__contenido
    {
        padding: 1rem 5rem;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 3rem;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__heading
    {
        font-size: 5rem;
        text-transform: uppercase;
    }

    &__texto
    {
        margin: 0;
        color: darken($gris, 20%);
        font-size: 2.2rem;

        &--saltolinea
        {
            display: block;
        }
    }

    &__boton
    {
        @include boton($negro, $blanco);
        text-transform: uppercase;

        @include tablet 
        {
            width: 50%;
        }
    }
}