h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  font-family: "Molle", cursive;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #0F0F0F;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Teko", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.header {
  position: relative;
  height: 100vh;
}
.header__video {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.header__video-vid {
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  object-fit: cover;
}
.header__video-mask {
  background: rgba(255, 255, 255, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.header__contenido {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header__titulo {
  font-size: 8rem;
  color: #5E0E00;
  text-align: center;
}
@media (min-width: 768px) {
  .header__titulo {
    font-size: 14.4rem;
  }
}
.header__opciones {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__opciones {
    flex-direction: row;
    gap: 20rem;
  }
}
.header__enlace {
  padding: 0.5rem 5rem;
  font-size: 2.8rem;
  border: 2px solid #CF1717;
  font-weight: 400;
  transition: all 0.4s ease-in-out;
}
.header__enlace:hover {
  background-color: #FFFFFF;
  border: 2px solid #5E0E00;
  cursor: pointer;
}
.header__scroll {
  margin-top: 10rem;
  background-color: #FFFFFF;
  padding: 1rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #5E0E00;
}
.header__scroll i {
  font-size: 2.8rem;
  font-weight: 500;
}
.header__scroll:hover {
  cursor: pointer;
}

.barra {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}
.barra__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra__menu {
  text-transform: uppercase;
  font-size: 2.8rem;
  font-weight: 500;
}
.barra__menu:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra__menu {
    display: none;
  }
}

.scroll {
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(0.5rem);
  transition: all 0.3s ease-in-out;
  padding-bottom: 1rem;
}
.scroll .barra__logo-imagen {
  width: 15rem;
}
.scroll .navegacion__enlace {
  font-size: 2rem;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  gap: 2rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 4rem;
    margin-top: 0;
  }
}
.navegacion__enlace {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 500;
  background-color: #0F0F0F;
  width: 100%;
  text-align: center;
  padding: 1rem 10rem;
  color: #FFFFFF;
  transition: all 0.3s ease-in-out;
  position: relative;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    background-color: unset;
    color: #0F0F0F;
    padding: 0;
  }
}
.navegacion__enlace:hover {
  color: #CF1717;
}
@media (min-width: 768px) {
  .navegacion__enlace--active::after {
    content: "";
    background-color: #CF1717;
    width: 100%;
    height: 0.3rem;
    bottom: -0.5rem;
    left: 0;
    position: absolute;
  }
}

.no-mostrar {
  display: none;
}
@media (min-width: 768px) {
  .no-mostrar {
    display: flex;
  }
}

.opciones {
  margin-top: 5rem;
}
.opciones__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.opcion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .opcion {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.opcion:nth-child(2) .opcion__imagen {
  grid-column: 2/3;
}
.opcion:nth-child(2) .opcion__contenido {
  grid-row: 1/2;
  grid-column: 1/2;
}
.opcion__contenido {
  padding: 1rem 5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 3rem;
}
@media (min-width: 768px) {
  .opcion__contenido {
    text-align: left;
  }
}
.opcion__heading {
  font-size: 5rem;
  text-transform: uppercase;
}
.opcion__texto {
  margin: 0;
  color: #8f8f8f;
  font-size: 2.2rem;
}
.opcion__texto--saltolinea {
  display: block;
}
.opcion__boton {
  background-color: #0F0F0F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .opcion__boton {
    width: auto;
  }
}
.opcion__boton:hover {
  background-color: black;
}
@media (min-width: 768px) {
  .opcion__boton {
    width: 50%;
  }
}

.carta {
  background-image: url("../img/carta_bg.webp");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: 10rem;
  position: relative;
}
@media (min-width: 1024px) {
  .carta {
    background-attachment: fixed;
  }
}
.carta__mask {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.5);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.carta__contenido {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10rem 20rem;
}
@media (min-width: 768px) {
  .carta__contenido {
    padding: 20rem 20rem;
  }
}
.carta__heading {
  font-size: 6rem;
  text-transform: uppercase;
}
.carta__texto {
  text-transform: uppercase;
  font-size: 3rem;
  text-align: center;
  font-weight: 500;
}
@media (min-width: 768px) {
  .carta__texto {
    font-size: 4rem;
  }
}
.carta__boton {
  background-color: #0F0F0F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .carta__boton {
    width: auto;
  }
}
.carta__boton:hover {
  background-color: black;
}
@media (min-width: 768px) {
  .carta__boton {
    width: 50%;
  }
}

.footer {
  background-color: #0F0F0F;
  padding: 5rem 10rem;
}
.footer__logo {
  margin: 0 auto;
}
.footer__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}
.footer__texto {
  color: #FFFFFF;
  font-size: 3rem;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}
.footer__texto span {
  display: block;
  font-size: 6rem;
}
.footer__socialmedia {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__socialmedia {
    flex-direction: row;
    gap: 5rem;
  }
}

.socialmedia__enlace img {
  width: 4rem;
}